.content-wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;

}

.header-wrapper {
    top: 0;
    left: 0;
    width: 100%;
}

.site-header {
    display: flex;
    flex-flow: row wrap;
    align-content: flex-end;
    justify-content: space-between;
}

.site-brand {
    display: flex;
    align-items: center;
}

.site-logo {
    width: 150px;
}

.site-logo img {
    max-width: 100%;
}



.site-social {
    display:  flex;
}

.site-social ul {
    display: flex;
}

.site-social li {
    padding-left: 10px;
}

.site-content,
.site-footer {
  color: white;
  padding: 1em;
}

.site-content {
  flex: 1;
}

.site-footer {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
}
