.site-nav {
    display: flex;
    align-items: center;
}

.site-nav ul {
    display: flex;
    align-items: flex-start;
}

.site-nav > ul > li {
  padding-left: 20px;
}

.site-nav li li {
  padding-left: 0;
}

.nav,
.nav-drop {
    align-self: center;
    position: absolute;
    max-height: 0;
}

.nav-drop {
    flex-direction: column;
    overflow: hidden;
    transition: 0.3s ease all;
    z-index: 1;
}

.site-nav li:hover > .nav-drop {
  max-height: 100%;
}

.open {
    max-height: none;
}

// Dropdown Nav indicator
.menu-item-has-children > a {
	&:after {
		content: "▾";
		font-size: $size-sml;
	}
}



// Breadcrumbs
.main-breadcrumbs {
  font-size: $size-sml;
  margin-bottom: $size-base;
  margin-top: $size-base;
  padding-bottom: $size-sml;
}
